import React from 'react';
import './App.css';
import axios from "axios";
import Typography from '@material-ui/core/Typography';

class Orgs extends React.Component {

    constructor(props) {
    super(props);

    this.state = {
      orgList: [],
    };

  }

  componentDidMount() {
      this.refreshList();
  }

  refreshList = () => {
      axios
        .get("/orgs/")
        .then(res => this.setState({ orgList: res.results }))
        .catch(err => console.log(err));
  };

  render() {
  return (
    <div className="App">

  		<Typography variant="h2" gutterBottom>
  		  Organizations
  		</Typography>

      {this.state.orgList}
    </div>
  );
  }
}

export default Orgs;
